.whiteGradient {
  /* background: linear-gradient(180deg, #FF335F 0px, #FFFFFF 40%, #FF335F 100%); */
   background: linear-gradient(
    180deg,
    #FF335F 0%,     /* Начинаем с розового */
    #FFFFFF 15%,    /* Быстрый переход к белому */
    #FFFFFF 90%,    /* Белый остается до 90% высоты */
    #FF335F 95%,    /* Быстрый переход обратно к розовому */
    #FF335F 100%    /* Заканчиваем розовым */
  );
  position: absolute;
  top: 0;
  height: 100%;
  width: calc(100vw);
  max-width: 374px;
  z-index: -1;
}

